@import url(https://fonts.googleapis.com/css?family=Open+Sans:300,400,700);
.wrap-content.characters {
  max-width: 1140px;
  margin: 40px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; }

article {
  width: 327px;
  font-family: Open Sans; }
  article a img {
    height: 231px;
    width: 327px;
    object-fit: cover;
    object-position: top; }
  article .subscription {
    position: relative; }
    article .subscription a .article__name {
      margin-top: 10px;
      font-weight: 600;
      font-size: 18px; }
    article .subscription .article__publisher {
      font-size: 10px;
      color: #C1C4CC;
      margin-bottom: 49px; }
    article .subscription .article__issues {
      font-size: 10px;
      color: #849ACC;
      position: absolute;
      top: 21px;
      right: 0; }

.button-wrap {
  flex: 1 1 100%;
  display: flex;
  justify-content: center; }
  .button-wrap button {
    margin: 29px auto;
    display: block;
    width: 386px;
    height: 45px;
    text-align: center;
    border: none;
    background: #2C467F;
    font-weight: bold;
    font-size: 14px;
    color: #fff; }

.wrap-content.character {
  max-width: 1140px;
  margin: 40px auto;
  display: flex;
  flex-wrap: wrap;
  justify-content: center; }

img.character_img {
  width: 416px;
  height: 610px;
  object-fit: cover;
  object-position: top; }

.charaction-description {
  padding: 30px;
  max-width: 530px; }
  .charaction-description .character__name {
    font-family: Open Sans;
    font-weight: bold;
    font-size: 48px;
    color: #2C467F; }
  .charaction-description .character__real-name {
    margin-top: 33px;
    padding-left: 90px;
    position: relative;
    font-size: 14px;
    color: #000000; }
    .charaction-description .character__real-name span {
      position: absolute;
      left: 0;
      color: #C1C4CC; }
  .charaction-description .character__publisher-name {
    padding-left: 90px;
    position: relative;
    font-size: 14px;
    color: #000000;
    margin-top: 6px; }
    .charaction-description .character__publisher-name span {
      position: absolute;
      left: 0;
      color: #C1C4CC; }
  .charaction-description .character__gender {
    padding-left: 90px;
    position: relative;
    font-size: 14px;
    color: #000000;
    margin-top: 6px; }
    .charaction-description .character__gender span {
      position: absolute;
      left: 0;
      color: #C1C4CC; }
  .charaction-description .character__update {
    padding-left: 90px;
    position: relative;
    font-size: 14px;
    color: #000000;
    margin-top: 6px; }
    .charaction-description .character__update span {
      position: absolute;
      left: 0;
      color: #C1C4CC; }
  .charaction-description .character__deck {
    margin-top: 33px;
    font-size: 18px;
    color: #000000;
    text-indent: 1.5em; }

@charset "UTF-8";
* {
  margin: 0;
  padding: 0; }

a {
  text-decoration: none;
  /* Отменяем подчеркивание у ссылки */ }

header {
  background: #52607F; }
  header .wrap-header {
    max-width: 1140px;
    margin: 0 auto;
    min-height: 40px;
    display: flex;
    align-items: center; }

.content {
  background: #fff; }

